article {
	margin-top: 142px;
	@include md {
		margin-top: 50px; } }
.article {
	background: #fff;
	color: $darkblue;
	border-radius: 16px;
	padding: 80px 80px 100px 100px;
	@include md {
		padding: 40px; }
	@include sm {
		padding: 30px 10px; }
	&__heading {
		padding-bottom: 35px;
		@include ms {
			text-align: center; } } }

.post {
	column-count: 2;
	column-gap: 70px;
	@include md {
		column-gap: 30px; }
	@include sm {
		column-count: 1; }
	&__icon {
		flex: 0 0 50px;
		margin-right: 25px;
		margin-top: 10px;
		@include md {
			margin-right: 10px; } }
	&__description {
		font-weight: 300;
		line-height: 2;
		flex-grow: 1;
		@include sm {
			line-height: 22px; }
		@include ms {
			max-width: 74%; } }
	@include md {
		margin-right: 0; }
	@include sm {
		max-width: none;
		width: 100%; } }
.post__item {
	display: inline-flex;
	&:not(:last-child) {
		margin-bottom: 40px; } }
