.forecast {
	margin: 140px auto 0;
	position: relative;
	&__bg {
		background-color: $darkblue;
		width: 500%;
		height: 245%;
		position: absolute;
		z-index: -5;
		@include rotate(30);
		left: -200%;
		top: -600px;
		@include md {
			top: -200px; }
		@include ms {
			top: -100px; } }
	&__container {
		max-width: 1010px;
		@include sm {
			padding: 0 15px; } }
	&__heading {
		padding: 8px 0; }
	&__description {
		@include f(24px, 600);
		margin-top: 8px;
		@include md {
			@include f(18px, 600); }
		@include sm {
			@include f(16px, 600); } }
	&__currency {
		display: flex;
		flex-wrap: wrap;
		margin-top: 35px;
		margin-right: -16px;
		@include sm {
			margin-top: 20px; } }
	&__box1 {
		position: absolute;
		top: -65px;
		right: 7.5%;
		z-index: -1;
		@include sm {
			display: none; } }
	&__box2 {
		position: absolute;
		top: 60px;
		left: 0;
		z-index: -1;
		@include md {
			top: 1280px;
			left: -150px;
			z-index: 1; }
		@include sm {
			display: none; } }
	@include md {
		margin-top: 40px; } }

.button-border {
	&__item {
		&:not(:last-child) {
			margin-right: 30px;
			margin-bottom: 30px;
			@include md {
				margin-right: 20px;
				margin-bottom: 20px; }
			@include sm {
				margin-right: 10px;
				margin-bottom: 10px; } } } }

.graph {
	margin: 10px auto 0;
	position: relative;
	&__img {
		display: block;
		margin: 0 auto;
		position: relative;
		z-index: -2;
		width: 100%;
		padding: 0 30px; }
	@include sm {
		margin-top: 20px; } }
.indicators {
	display: flex;
	position: absolute;
	left: 27.4%;
	top: 5.5%;
	line-height: 1.3;
	&__item {
		margin-right: 45px;
		&:last-child {
			margin-right: 0; } }
	@include md {
		left: 10%; }
	@include sm {
		position: static;
		justify-content: center;
		margin-bottom: 20px; } }

.indicator {
	&__main {
		text-transform: uppercase;
		@include f(24px, 700);
		@include md {
			@include f(20px, 700); }
		@include sm {
			@include f(18px,700); }
		&_centered {
			line-height: 2.6; } }
	&__aside {
		@include f(12px, 500);
		text-align: right;
		margin-left: -15px;
		@include md {
			@include f(10px, 500); }
		& span {
			color: $green; }
		&_arrow {
			margin-bottom: 2px; } } }
