.heading {
	@include f(72px, 600);
	@include md {
		@include f(48px, 600); }
	@include sm {
		@include f(36px, 600); }
	&_white {
		color: $white; }
	&_blue {
		color: $darkblue; } }


.title-mid {
	@include f(60px, 600);
	@include md {
		font-size: 48px; }
	@include sm {
		font-size: 30px; } }

.title-smaller {
	@include f(30px, 600);
	line-height: 1.25;
	@include md {
		@include f(24px, 600); } }


.subheading {
	@include f(48px, 600);
	@include md {
		@include f(36px, 600); }
	@include sm {
		@include f(30px, 600); } }

.heading-small {
	@include f(36px, 600);
	@include md {
		@include f(27px, 600); } }

.heading-thin {
	font-weight: 200;
	display: block; }

.descr {
	// +f(18px, 300)
	line-height: 2;
	&_dark {
		color: $black; }
	@include md {
		// +f(14px, 300)
		line-height: 1.6; }
	@include sm {
 } }		// +f(14px, 300)
