.contacts {
	padding-top: 50px;
	&__container {
		max-width: 1040px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		@include md {
			max-width: 840px; }
		@include sm {
			flex-wrap: wrap;
			align-items: center;
			flex-direction: column;
			padding: 0 15px; }
		@include ms {
			padding-bottom: 60px; } }
	&__left {
		@include sm {
			order: 2; } } }
.subscribe-form {
	border: 16px solid $lightnavy;
	border-radius: 8px;
	padding: 45px 60px 55px;
	line-height: 1;
	max-width: 500px;
	@include md {
		padding: 30px 35px 40px;
		max-width: 380px; }
	@include sm {
		border: 12px solid $lightnavy;
		padding: 28px 28px 35px;
		margin-bottom: 30px;
		display: flex;
		flex-direction: column;
		align-items: center;
		width: 100%; }
	&__title {}
	&__input {
		margin: 35px 0 48px;
		width: 100%;
		padding: 15px;
		background: transparent;
		border-bottom: 2px solid $lightnavy;
		color: $white;
		@include f(24px, 600);
		@include md {
			font-size: 18px; }
		@include sm {
			font-size: 16px; }
		@include placeholder {
			color: $white;
			@include f(24px, 600);
			@include md {
				font-size: 18px; }
			@include sm {
				font-size: 16px; } }
		@include sm {
			margin: 10px 0 25px; } }
	&__button {
		@include hover {
			background: lighten($darkblue, 5%); } } }

.contact-btn {
	width: 410px;
	text-align: center;
	padding: 20px;
	@include md {
		width: 304px; }
	&__item {
		&:not(:last-child) {
			margin-bottom: 25px; } }
	&__link {
		&_mail {
			@include f(18px, 600); }
		@include hover {
			.icon-telegram {
				fill: $white; } } } }



.icon-telegram {
	fill: $darkblue;
	margin-right: 8px;
	position: relative;
	top: 4px;
	@include tr(fill 300ms); }
