.faq {
	margin: 112px 0;
	&__container {
		@include sm {
			padding: 0 10px; } }
	&__img {
		position: absolute;
		top: 100px;
		left: -7.7%;
		@include md {
			left: 80%;
			top: -70px; }
		@include sm {
			left: -70px;
			top: -85px; } }
	&__block-wrapper {
		background: $white;
		color: $darkblue;
		border-radius: 16px;
		max-width: 1170px;
		padding: 80px;
		padding-left: 100px;
		position: relative;
		@include md {
			padding: 40px 35px; }
		@include sm {
			padding: 30px 10px 30px 20px; } }
	&__heading {
		line-height: 1;
		margin-bottom: 50px; }
	@include md {
		margin-bottom: 0; }
	@include sm {
		margin-top: 70px; } }

.accordion {
	&__item {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		cursor: pointer;
		padding-bottom: 27px;
		line-height: 1.5;
		&:last-child {
			padding-bottom: 50px; } }
	&__circle {
		@include circle(36);
		align-self: flex-start;
		background: $navy;
		flex: none;
		position: relative;
		margin-right: 16px;
		margin-top: 5px; }
	&__arrow {
			@include vcenter;
			&_active {
				@include rotate(-90); } }
	&__question {
		@include nosel;
		@include f(30px, 600);
		max-width: 92%;
		@include md {
			@include f(24px, 600);
			max-width: 90%; }
		@include sm {
			max-width: 80%;
			@include f(18px, 600); } }
	&__answer {
		width: 100%;
		padding-top: 12px;
		padding-left: 50px;
		display: none;
		background-color: white;
		overflow: hidden;
		cursor: text;
		line-height: 2; } }
