.nav {
	@include md {
		display: none; }
	&__list {
		display: flex; }
	&__item {
		margin-right: 25px;
		&:last-child {
			margin-right: 30px;
			@include md {
				margin-right: 20px; } }
		@include md {
			margin-right: 18px; } }
	&__link {
		border-bottom: 2px solid transparent;
		@include tr(300ms);
		padding: 5px;
		@include hover {
			border-bottom: 2px solid $white; }
		@include md {
			padding: 2px; } } }

.current {
	border-bottom: 2px solid $white; }

// OPENED
.nav-opened {
	@include md {
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		background: $dot;
		z-index: 3;
		padding: 70px 0 20px; } }
.nav__list {
	@include md {
		flex-direction: column;
		justify-content: center;
		width: 100%; } }
.nav__item {
	@include md {
		text-align: center;
		padding: 5px; } }
.nav__link {
	@include md {
		border-bottom: 2px solid $darkblue;
		color: $darkblue; } }
